<template>
  <div id="app">
    <a-steps :current="current">
      <a-step
        v-for="item in steps"
        :key="item.title"
        :title="item.title"
      />
    </a-steps>
    <div class="steps-content">
      <div v-if="current==0">
        <!-- input 8 pictures -->
        <a-upload-dragger
          :multiple="true"
          :fileList="fileList"
          :remove="handleRemove"
          :beforeUpload="beforeUpload"
        >
          <p class="ant-upload-drag-icon">
            <a-icon type="inbox" />
          </p>
          <p class="ant-upload-text">Click or drag file to this area to upload</p>
          <p class="ant-upload-hint">
            You must pick up <span style='font-size:18px;color:black;'>5 outside pictures</span> for inference
          </p>
        </a-upload-dragger>
        <div style="margin-top:30px">
          <a-upload-dragger
            :multiple="true"
            :fileList="fileList_inside"
            :remove="handleRemove_inside"
            :beforeUpload="beforeUpload_inside"
          >
            <p class="ant-upload-drag-icon">
              <a-icon type="inbox" />
            </p>
            <p class="ant-upload-text">Click or drag file to this area to upload</p>
            <p class="ant-upload-hint">
              You must pick up <span style='font-size:18px;color:black;'>3 inside pictures</span> for inference
            </p>
          </a-upload-dragger>
        </div>

        <a-button
          @click="handleUpload"
          style='margin-top:30px'
          :disabled='fileList.length != numberOfPic || fileList_inside.length != numberOfPic_inside'
          type="primary"
        >Inference</a-button>
      </div>
      <div v-else-if="current==1">
        <!-- loading -->
        <a-spin tip="wait a second" />
      </div>
      <div
        v-else-if="current==2"
        class="display-result"
      >
        <!-- display result -->
        <a-card
          hoverable
          style="width:400px;margin:30px;"
          v-for="(item, index) in result.outside"
          :key="index"
        >
          <img
            alt="example"
            :src="item.image"
            slot="cover"
            @click="open(item)"
          />
          <a-card-meta :title="item.isPass?'Pass':'Non Pass'">
            <template slot="description">Status: {{item.defects | nameDefect}}</template>
          </a-card-meta>
        </a-card>
        <a-card
          hoverable
          style="width:400px;margin:30px;"
          v-for="(item, index) in result.inside"
          :key="index"
        >
          <img
            alt="example"
            :src="item.image"
            slot="cover"
            @click="open(item)"
          />
          <a-card-meta :title="item.isPass?'Pass':'Non Pass'">
            <template slot="description">Status: {{item.defects | nameDefectInside}}</template>
          </a-card-meta>
        </a-card>
      </div>
    </div>
    <a-modal
      title="warning"
      v-model="visible"
      @ok="handleOk"
    >
      <p>You must upload 8 pictures for inference,but there are {{fileList.length}} pictures
      </p>
    </a-modal>
  </div>
</template>

<script>
export default {
  name: "App",
  data() {
    return {
      current: 0,
      fileList: [],
      fileList_inside: [],
      visible: false,
      uploading: false,
      numberOfPic: 5,
      numberOfPic_inside: 3,
      steps: [
        {
          title: "Upload pictures",
          content: "First-content"
        },
        {
          title: "Processing",
          content: "Second-content"
        },
        {
          title: "Done",
          content: "Last-content"
        }
      ],
      result: {}
    };
  },
  filters: {
    nameDefect(defects) {
      const cls_name = {
        1: "container",
        2: "slightly rusting",
        3: "serious rusting",
        4: "slightly deformation",
        5: "serious deformation",
        6: "container ID"
      };
      return defects.map(item => cls_name[item]);
    },
    nameDefectInside(defects) {
      const cls_name = {
        1: "slightly rusting",
        2: "serious rusting",
        3: "slightly deformation",
        4: "serious deformation",
        6: "oil contamination",
        7: "bad sealing",
        8: "novel object",
        9: "damaged floor"
      };
      return defects.map(item => cls_name[item]);
    }
  },
  methods: {
    open(item) {
      window.open(item.image, "_blank");
    },
    handleChange(info) {
      const status = info.file.status;
      console.log(status);
    },
    handleRemove(file) {
      const index = this.fileList.indexOf(file);
      const newFileList = this.fileList.slice();
      newFileList.splice(index, 1);
      this.fileList = newFileList;
    },
    beforeUpload(file) {
      this.fileList = [...this.fileList, file];
      return false;
    },
    handleRemove_inside(file) {
      const index = this.fileList_inside.indexOf(file);
      const newFileList = this.fileList_inside.slice();
      newFileList.splice(index, 1);
      this.fileList_inside = newFileList;
    },
    beforeUpload_inside(file) {
      this.fileList_inside = [...this.fileList_inside, file];
      return false;
    },
    handleUpload() {
      const {
        fileList,
        numberOfPic,
        fileList_inside,
        numberOfPic_inside
      } = this;
      if (
        fileList.length != numberOfPic &&
        fileList_inside.length != numberOfPic_inside
      ) {
        this.visible = true;
        return;
      } else {
        this.current = 1;
      }
      const formData = new FormData();
      fileList.forEach(file => {
        formData.append("file", file);
      });
      fileList_inside.forEach(file => {
        formData.append("file_inside", file);
      });
      this.uploading = true;
      let config = {
        headers: {
          "Content-Type": "multipart/form-data"
        }
      };
      this.$axios
        .post("/predictContainer", formData, config)
        .then(response => {
          console.log(response);
          this.current = 2;
          // response.data
          this.result = response.data.data;
        })
        .catch(res => {
          this.current = 0;
          this.$message.error(
            "There is an error in processing,try again please"
          );
          console.log(res);
        });
    },
    handleOk(e) {
      console.log(e);
      this.visible = false;
    }
  }
};
</script>
